import { _decorator, Component, Node, SpriteFrame } from 'cc';
import { GestureData } from './GestureData';
const { ccclass, property } = _decorator;

@ccclass('GestureDataBase')
export class GestureDataBase extends Component {

    @property(SpriteFrame)
    sprite : Array<SpriteFrame> = new Array<SpriteFrame>();

    data : Array<GestureData> = new Array<GestureData>();

    start(){
        for(var i = 0; i < 6; i++){
            var temp = new GestureData();
            temp.mSprite = this.sprite[i];
            temp.mAnswer = i;
            temp.mIndex = i;
            this.Add(temp);
        }
    }

    public Add(data : GestureData){
        this.data.push(data);
    }

    public GetAllData() : Array<GestureData>{
        return this.data;
    }

    public GetData(index : number) : GestureData{
        return this.data[index];
    }

}

